import { FactoryFunction, FactoryObject } from '../../types/dash/Factory'
import FactoryMaker from '../FactoryMaker'

class URLUtils {
  private config: FactoryObject
  constructor(ctx: FactoryObject, ...args: any[]) {
    this.config = ctx.context
  }

  setup() {}

  resolve(...urls: string[]): string {
    let index = 0
    let str = ''
    while (index < urls.length) {
      let url = urls[index]
      // å¦æurlä¸ä»¥/æè./,../è¿ç§å½¢å¼å¼å¤´çè¯
      if (/^(?!(\.|\/))/.test(url)) {
        if (str[str.length - 1] !== '/' && str !== '') {
          str += '/'
        }
      } else if (/^\/.+/.test(url)) {
        // å¦æurlä»¥/å¼å¤´
        if (str[str.length - 1] === '/') {
          url = url.slice(1)
        }
      } else if (/^(\.).+/.test(url)) {
        //TODOï¼ å¦æurlä»¥ ./,../,../../ä¹ç±»çå½¢å¼å¼å¤´
      }
      str += url
      index++
    }
    return str
  }

  sliceLastURLPath(url: string): string {
    for (let i = url.length - 1; i >= 0; i--) {
      if (url[i] === '/') {
        return url.slice(0, i)
      }
    }
    return url
  }
}

const factory = FactoryMaker.getSingleFactory(URLUtils)
export default factory
export { URLUtils }
